.nav-row {
    width: 66%;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: rgb(255, 0, 0);
  text-shadow: 0px 0px 2px #000;
  margin-bottom: 50px;
  padding-top: 15vh;
  padding-bottom: 0px;
  margin-bottom: 0px;
}

.jumbotron {
  background-image: url("https://images.megapixl.com/1272/12720608.jpg");
  overflow: hidden;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 25vh;
  margin: 1rem;
  position: relative;
  width: 20%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
} 

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  vertical-align: middle;
}
.card > .img-container > img {
  height: 100%;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

